import { connect } from 'react-redux';
import RecentActivityComponent from './RecentActivityComponent';
import {
  recentActivitySelector,
  selectedRecentActivitySelector
} from '../../redux/selectors/recentActivitySelector';
import { formValueSelector, change } from 'redux-form';
import { typeFilter, dateFilter } from '../../helpers/activityFilters';
import { setActivePage } from '../../redux/actions/paginationActions';
import config from '../../config';

const getRecentActivity = (filterForSelected, state) => {
  if (filterForSelected) {
    return selectedRecentActivitySelector(state);
  }

  return recentActivitySelector(state);
};

const mapStateToProps = (state, ownProps) => {
  const formValue = formValueSelector('recent-activity');
  const recentActivity = getRecentActivity(ownProps.filterForSelected, state);
  const type = formValue(state, 'type');
  const startDate = formValue(state, 'startDate');
  const endDate = formValue(state, 'endDate');
  const { usePagination } = config.OrderHistoryData;

  return {
    recentActivity: recentActivity
      .filter(item => typeFilter(item, type))
      .filter(item => {
        return dateFilter(item, startDate, endDate);
      }),
    startDate: formValue(state, 'startDate'),
    endDate: formValue(state, 'endDate'),
    usePagination
  };
};

const mapDispatchToProps = dispatch => ({
  setEndDate: value => dispatch(change('recent-activity', 'endDate', value)),
  clearFilters: () => {
    dispatch(change('recent-activity', 'startDate', null));
    dispatch(change('recent-activity', 'endDate', null));
    dispatch(change('recent-activity', 'type', 'all'));
  },
  resetActivePage: () =>
    dispatch(
      setActivePage({ componentName: 'RecentActivityComponent', activePage: 0 })
    )
});

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(RecentActivityComponent);



