import React from 'react';
import { reduxForm } from 'redux-form';
import PropTypes from 'prop-types';
import { getBEMClasses } from '../../helpers/cssClassesHelper';
import APTable from '../common/APTable';
import APSelect from '../common/APSelect/APSelect';
import APDatePicker from '../common/APDatePicker/APDatePicker';
import APButton from '../common/APButton';
import { Action, Amount, Status, DateTime } from './ActivityCellComponents';

import './ActivityComponents.css';

const baseClasses = getBEMClasses('activity');

const formatActivity = (activity, context) => {
  return activity.map(item => {
    const { Type } = item;
    switch (Type) {
      case 'deposit':
      case 'withdraw': {
        const { Product, ProductType, Status } = item.Action.params;
        item.Action = {
          ...item.Action,
          primaryText:
            Type === 'deposit'
              ? context.t('Deposited {a}', { a: Product })
              : context.t('Withdrew {a}', { a: Product }),
          secondaryText:
            ProductType === 'CryptoCurrency'
              ? Type === 'deposit'
                ? context.t('from External Address')
                : context.t('to External Address')
              : Type === 'deposit'
                ? context.t('from Bank')
                : context.t('to Bank')
        };
        item.Status = {
          ...item.Status,
          status: context.t('{a}', { a: Status })
        };
        return item;
      }
      case 'send':
      case 'receive': {
        const {
          Product,
          ReceiverUserName,
          SenderUserName
        } = item.Action.params;
        item.Action = {
          ...item.Action,
          primaryText:
            Type === 'send'
              ? context.t('Sent {a}', { a: Product })
              : context.t('Received {a}', { a: Product }),
          secondaryText:
            Type === 'send'
              ? context.t(`to ${ReceiverUserName}`)
              : context.t(`to ${SenderUserName}`)
        };
        item.Status = {
          ...item.Status,
          status: context.t('Complete')
        };
        return item;
      }
      case 'trades': {
        const { Product1Symbol, Product2Symbol, Side } = item.Action.params;
        item.Action = {
          ...item.Action,
          primaryText:
            Side === 'Buy'
              ? context.t('Bought {a}', { a: Product1Symbol })
              : context.t('Sold {a}', { a: Product1Symbol }),
          secondaryText:
            Side === 'Buy'
              ? context.t('with {a}', { a: Product2Symbol })
              : context.t('for {a}', { a: Product2Symbol })
        };
        item.Status = {
          ...item.Status,
          status: context.t('Complete')
        };
        return item;
      }
      default:
        return item;
    }
  });
};

let RecentActivityComponent = (props, context) => {
  const columns = [
    {
      header: context.t('Action'),
      cell: row => <Action value={row.Action} showProductIcon />
    },
    {
      header: context.t('Amount'),
      width: '20rem',
      cell: row => <Amount value={row.Amount} />
    },
    {
      header: context.t('Status / ID'),
      width: '40rem',
      cell: row => <Status value={row.Status} />
    },
    {
      header: context.t('Time & Date'),
      width: '15rem',
      cell: row => <DateTime value={row.TimeStamp} />
    }
  ];
  const {
    endDate,
    setEndDate,
    clearFilters,
    resetActivePage,
    usePagination
  } = props;

  return (
    <div className={baseClasses()}>
      <div className={baseClasses('header-bar')}>
        <div className={baseClasses('header-text')}>
          {context.t('Recent Activity')}
        </div>
        <form className={baseClasses('select-buttons')}>
          <APSelect
            name="type"
            customClass={baseClasses()}
            label={context.t('Activity type:')}
            options={[
              { value: 'all', label: context.t('All Activities') },
              { value: 'trades', label: context.t('Trades') },
              { value: 'withdraw', label: context.t('Withdrawals') },
              { value: 'deposit', label: context.t('Deposits') }
            ]}
            onSelect={resetActivePage}
          />

          <APDatePicker
            name="startDate"
            customClass={baseClasses('from')}
            customChange={value => {
              resetActivePage();
              if (value && !endDate) {
                setEndDate(new Date());
              }
            }}
            label={context.t('Date range:')}
            format={value => value}
            maxDate={new Date()}
          />

          <APDatePicker
            name="endDate"
            customClass={baseClasses('to')}
            label={context.t('to')}
            format={value => value}
            customChange={resetActivePage}
            maxDate={new Date()}
          />

          <APButton
            customClass={baseClasses()}
            classModifiers="clear-filters"
            type="button"
            onClick={clearFilters}>
            {context.t('Clear Filters')}
          </APButton>
        </form>
      </div>
      <APTable
        {...{
          componentName: 'RecentActivityComponent',
          columns,
          pageSize: 6,
          minRow: 6,
          rows: formatActivity(props.recentActivity, context),
          baseClass: baseClasses,
          headerOutside: true,
          usePagination,
          empty: context.t('No data is available')
        }}
      />
    </div>
  );
};

RecentActivityComponent.contextTypes = {
  t: PropTypes.func.isRequired
};

export default reduxForm({
  form: 'recent-activity',
  destroyOnUnmount: false,
  initialValues: {
    type: 'all',
    startDate: undefined,
    endDate: undefined
  }
})(RecentActivityComponent);



