import getTime from 'date-fns/get_time';
import isWithinRange from 'date-fns/is_within_range';
import isDate from 'date-fns/is_date';
import get from 'lodash/get';
import addDays from 'date-fns/add_days';

export const typeFilter = (item, selectedType) => {
  const value = get(item, 'Type');
  if (!value) {
    console.warn('recent activity item does not have a type:', item);
    return false;
  }

  if (selectedType !== 'all') {
    return value === selectedType;
  } else {
    return true;
  }
};

export const dateFilter = (item, startDate = 0, endDate = Date.now()) => {
  const rawTimeStamp = get(item, 'TimeStamp.raw');

  if (!rawTimeStamp) {
    console.warn(
      'recent activity item does not have a TimeStamp.raw property:',
      item
    );
    return false;
  }

  if (isDate(startDate) && isDate(endDate)) {
    return isWithinRange(
      rawTimeStamp,
      getTime(startDate),
      getTime(addDays(endDate, 1))
    );
  }

  return isWithinRange(rawTimeStamp, 0, Date.now());
};



